@import "src/variables";

.UserInfo {
  display: flex;
  gap: 18px;
  padding-top: 180px;

  &Avatar {
    width: 80px;
    height: 80px;
    border-radius: 16px;
  }

  &Info {
    display: flex;
    flex-direction: column;
    gap: 12px;

    &Name {
      font-size: 20px;
      font-weight: 400;
      line-height: 32px;
      color: $font-color;
    }

    &Position {
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: rgba($font-color, 0.7);
    }
  }
}
