@import "src/variables";

.Work {
  background-color: #FFFFFF;
  border-radius: 16px;
  padding: 24px;
  display: flex;
  gap: 40px;
  flex-wrap: wrap;

  &About {
    max-width: calc(100% - 656px);
    min-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &Content {
      &Title {
        font-size: 20px;
        font-weight: 500;
        line-height: 32px;
        text-wrap: none;
        white-space: nowrap;
        padding-bottom: 61px;
      }

      &Description {
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        padding-bottom: 26px;
      }

      &Fields {
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        padding-bottom: 15px;

        &Title {
          color: rgba($font-color, 0.5);
        }
      }
    }

    &Footer {
      display: flex;
      gap: 32px;

      &Link {
        color: $font-color;
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;

        &:hover {
          color: $accent-color;
        }
      }

      &Disabled {
        color: rgba($font-color, 0.5);
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
      }
    }
  }

  &Picture {
    width: 616px;
    border-radius: 16px;
  }
}
