@import "src/variables";

.Timeline {
  padding-top: 130px;

  &Header {
    font-size: 20px;
    line-height: 32px;
    font-weight: 400;
    color: $font-color;
    padding-bottom: 48px;
  }

  &Content {
    display: flex;
    flex-direction: column;
    gap: 40px
  }

  &Record {
    display: flex;
    gap: 32px;

    &Period {
      max-width: 144px;
      min-width: 144px;
      color: rgba($font-color, 50%);
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
    }

    &Info {
      display: flex;
      flex-direction: column;
      gap: 4px;

      &Title {
        color: $font-color;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
      }

      &Description {
        color: rgba($font-color, 0.7);
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
